import React from "react";
import { NavLink } from "react-router-dom";
// import Icon from "@material-ui/core/Icon";
import HomeIcon from "@material-ui/icons/Home";

import { IconButton } from "@material-ui/core";
import InfoIcon from "@material-ui/icons/Info";
import RoomServiceIcon from "@material-ui/icons/RoomService";
import ContactMailIcon from "@material-ui/icons/ContactMail";


    


const Navbar = () => {
  return (
    <>
      <nav class="navbar navbar-expand-lg navbar-light bg-light">
        <div class="container-fluid">
          <a className="navbar-brand" href="#">
            inzi <i class="bi bi-bag-dash"></i>
          </a>
          <button
            class="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span class="navbar-toggler-icon"></span>
          </button>
          <div class="collapse navbar-collapse" id="navbarSupportedContent">
            <ul class="navbar-nav me-auto mb-2 mb-lg-0">
              <li class="nav-item">
                <NavLink
                  className="nav-link active"
                  activeClassName="menu-style"
                  aria-current="page"
                  exact
                  to="/"
                 style={{color:"violet"}}>
                  <IconButton style={{color:"violet"}}>
                    <HomeIcon />
                  </IconButton>
                  Home
                </NavLink>
              </li>
              <li class="nav-item">
                <NavLink
                  className="nav-link active"
                  activeClassName="menu-style"
                  aria-current="page"
                  exact
                  to="/about"
                  style={{color:"blueviolet"}} >
                  <IconButton >
                    <InfoIcon style={{color:"blueviolet"}} />
                  </IconButton>
                  About
                </NavLink>
              </li>
              <li class="nav-item">
                <NavLink
                  className="nav-link active"
                  activeClassName="menu-style"
                  aria-current="page"
                  exact
                  to="/service"
                  style={{color:"green"}}>
                  <IconButton>
                    <RoomServiceIcon style={{color:"green"}} />
                  </IconButton>
                  Service
                </NavLink>
              </li>
              <li class="nav-item">
                <NavLink
                  className="nav-link active"
                  activeClassName="menu-style"
                  aria-current="page"
                  exact
                  to="/contact"
                  style={{color:"red"}}>
                  <IconButton>
                    <ContactMailIcon style={{color:"yellowgreen"}} />
                  </IconButton>
                  Contact
                </NavLink>
              </li>
            </ul>
            <form class="d-flex">
            <div class="input-group">
  <div class="form-outline">

  <input type="search" id="form1" class="form-control" placeholder="search anything"   />
   
  </div>
  <button type="button" class="btn btn-primary">
   <i class="bi bi-search"></i>
  </button>
</div>
        
            </form>
          </div>
        </div>
      </nav>
    </>
  );
};
export default Navbar;